.cell {
    width: 40px;
    height: 40px;
    padding: 0;
    border: none;
    background-color: #fff;
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
}

/* ROTATIONS */
.r-0 {
    transform: rotate(0);
}

.r-1 {
    transform: rotate(90deg);
}

.r-2 {
    transform: rotate(180deg);
}

.r-3 {
    transform: rotate(270deg);
}

